import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			userPass : ''
		};
	},
	methods : {
		goChange(){
			if(this.userPass == ''){
				uni.showModal({
					title:'提示',
					content:'请输入密码',
					showCancel:false
				})
				return;
			}
			uni.showModal({
				title: '警告',
				content: '是否确认注销账户',
				success: function (res) {
					if (res.confirm) {
						uni.showLoading({
							title : '正在注销'
						})
						http.get('member/destroyMember',{phone:this.$store.state.userInfo.phone}).then((res)=>{
							_this.$store.commit('mainPageNumUpdate',1);
							_this.$store.commit('userGoOut',{});
							uni.reLaunch({
								url: '/pages/checkpoint/login/login?msg=已注销'
							});
						})
					}
				}
			});
		}
	}
}
